.order-head {
  width: 75vw;
  margin: auto;
  margin-left: 0vw;
  margin-bottom: 1vw;
  margin-top: 1.5vh;
  height: 5.5vh;
  display: flex;
  justify-content: space-evenly;
}

.heading {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  color: #ffffff;
  line-height: 5vh;
  background: rgb(255, 102, 0);
}

.name {
  width: 12.5vw;
  text-align: center;
}

.odrs {
  width: 6.5vw;
  text-align: center;
}

.status {
  width: 8.5vw;
  text-align: center;
}
.handled {
  width: 14vw;
}
